import {FC} from "react";
import "./SpecProjectsPreview.scss";
import FeedCard from "../feedCard";

interface SpecProjectsPreviewProps {

}

const SpecProjectsPreview: FC<SpecProjectsPreviewProps> = ({}) => {
  return (
    <section className={'specProjectsPreview'}>
      <div className={'specProjectsPreview__info info'}>
        <h2 className={'info__heading'}>Спецпроекты</h2>
        <div className={'info__list list'}>
          <div className={'list__item item'}>
            <h3 className={'item__heading item__heading_orange'}>Выбор редакции</h3>
            <FeedCard type={'slider'} heading={'К "экватору" выборов в Волгоградской области явка выросла на 10%'}/>
          </div>
          <div className={'list__item item'}>
            <h3 className={'item__heading item__heading_white'}>Выбор читателей</h3>
            <FeedCard type={'slider'} heading={'Кировский и Советский районы Волгограда соединят новой дорогой за 380 миллионов'}/>
          </div>
        </div>
      </div>
    </section>
  )
}

export default SpecProjectsPreview;